      <Icon type="stats-bars" :size="iconSize"></Icon>
      <span class="layout-text"> 网站概况 </span>
    </Menu-item>
    <Submenu name="2">
      <template slot="title">
        <Icon type="ios-cog"></Icon>
        系统配置
      </template>
      <Menu-item name="websetting"> 全局配置 </Menu-item>
      <Menu-item name="seosetting"> SEO优化 </Menu-item>
      <Menu-item name="advertisement"> 广告管理 </Menu-item>
      <Menu-item name="blogroll"> 友情链接 </Menu-item>
    </Submenu>
    <Submenu name="3">
      <template slot="title">
        <Icon type="ios-people"></Icon>
        人员管理
      </template>
      <Menu-item name="user"> 用户管理 </Menu-item>
      <Menu-item name="group"> 用户组管理 </Menu-item>
