<template>
  <div id="home">
    <layout-top></layout-top>
    <Row type="flex">
      <i-col :span="spanLeft" class="layout-menu-left">
        <!--<div class="menu-hiden-button" style="text-align: center;">-->
          <!--<i-button type="text" @click.native="toggleClick">-->
            <!--<Icon type="navicon" size="32"></Icon>-->
          <!--</i-button>-->
        <!--</div>-->
        <layout-menu @size="iconSize"></layout-menu>
      </i-col>
      <i-col :span="spanRight">
        <transition name="slide-fade">
          <router-view></router-view>
        </transition>
        <layout-footer></layout-footer>
      </i-col>
    </Row>

  </div>
</template>

<script>
  import layoutTop from '../commons/Header.vue'
  import layoutMenu from '../commons/Menu.vue'
  import layoutFooter from '../commons/Footer.vue'

  export default {
    data () {
      return {
        spanLeft: 3,
        spanRight: 21
      }
    },
    components: { layoutTop, layoutMenu, layoutFooter },
    computed: {
      iconSize () {
        return this.spanLeft === 3 ? 21 : 23
      }
    },
    methods: {
      toggleClick () {
        if (this.spanLeft === 3) {
          this.spanLeft = 1
          this.spanRight = 23
        } else {
          this.spanLeft = 3
          this.spanRight = 21
        }
      }
    }
  }
</script>

<style>
 .layout-menu-left {
   border-top: 1px solid #f0f0f0;
   background-color: #464c5b;
   position: relative;
   top: 0;
   bottom: 0;
 }
 .menu-hiden-button {
   border-top: 1px solid #f0f0f0;
 }
  .ivu-row-flex {
    position: absolute;
    top: 60px;
    bottom: 0;
    width: 100%;
  }
 .slide-fade-enter-active {
   transition: all .8s ease;
 }
 .slide-fade-leave-active {
   display: none;
 }
 .slide-fade-enter, .slide-fade-leave-active {
   transform: translateX(10px);
   opacity: 0;
 }
 .breadcrumb-box {
   border-top: 1px solid #f0f0f0;
   padding: 10px 15px 15px;
   width: 100%;
   text-align: left;
   background-color: #f5f7f9;
 }
 .table-box {
   width: 98%;
   margin: 10px 1%;
   position: relative;
   top: 0;
   bottom: 0;
   overflow: hidden;
 }
 .table-page {
   display: block;
   float: right;
   margin: 15px 10px;
 }
 .text-success {
   color: #00cc66!important;
 }
 .text-danger {
   color: #ff5c33!important;
 }
 .text-grey {
   color: #c3cbd6!important;
 }
</style>



