<template>
  <div class="layout-top">
    <div class="top-logo">
      <p> 龙帝后台管理系统 </p>
    </div>
    <Menu mode="horizontal" theme="dark" active-name="1" @on-select="onRoute">
      <Menu-item name="1">
        <Badge :count="notice" overflow-count="99">
          <a style="color: #f0f0f0;padding: 0 5px;" href="#">
            <Icon type="ios-paper" size="28"></Icon>
          </a>
        </Badge>
      </Menu-item>
      <Menu-item name="2">
        <Badge v-if="message > 0" dot>
          <a style="color: #f0f0f0;" href="#">
            <Icon type="ios-bell-outline" size="28"></Icon>
          </a>
        </Badge>
        <Badge v-else>
          <Icon type="ios-bell-outline" size="16"></Icon>
        </Badge>
      </Menu-item>
      <Submenu name="3">
        <template slot="title">
          <Icon type="person"></Icon>
          {{ getAlias }}
        </template>
        <Menu-group title="--用户配置--">
          <Menu-item name="3-1"> 个人信息 </Menu-item>
          <Menu-item name="3-2"> 修改密码 </Menu-item>
          <Menu-item name="3-3"> 登录日志 </Menu-item>
        </Menu-group>
        <Menu-group title="----其他----">
          <Menu-item name="3-4"> 转到前台 </Menu-item>
          <Menu-item name="outLogin"> 注销登录 </Menu-item>
        </Menu-group>
      </Submenu>
    </Menu>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        notice: 100,
        message: 2
      }
    },
    methods: {
      onRoute (s) {
        if (s === 'outLogin') {
          sessionStorage.removeItem('access_token')
          return this.$router.push('/')
        } else {
          return this.$router.push(s)
        }
      }
    },
    computed: {
      getAlias () {
        let userInfo = sessionStorage.getItem('user')
        if (!userInfo) return this.$router.push('/login')
        userInfo = JSON.parse(userInfo)
        return userInfo.alias
      }
    }
  }
</script>

<style scoped>
  .layout-top {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    background-color: #464c5b;
  }
  .layout-top>ul {
    float: right;
    margin-right: 3%;
  }
  .top-logo {
    width: 150px;
    max-width: 33%;
    height: 60px;
    line-height: 60px;
    float: left;
    margin-left: 30px;
  }
  .top-logo>p {
    color: #f0f0f0;
    font-size: 16px;
  }
</style>



